
.modal-content{ 
    background-color: transparent;
    position: relative;
    border:none;
    
    
}
.modal-content h2{
    color:white;
    display: flex;
    justify-content: center;
    text-align: center;
}

.modal-content p{
    margin-top: 15px;
    color:white;
    width: fit-content;
}
.btn{
    
    color:white;
    width: fit-content;
    font-size: 25px;
    position: absolute;
    right:0;
    top:0;
   margin-bottom:3px;
   padding-top: 0;
    
}