.projects-main {
  background-color: transparent;
  position: relative;
  border: none;
  height: 100vh;
  width: 100%;
}
.projects-main h2 {
  color: white;
  text-align: center;
}

.project1 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 30px;
}
.calc-img {
  width: 100px;
  margin-bottom: 10px;
}
.button-wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex: 1;
}
.button-wrapper button {
  border-radius: 3px 3px 3px 3px;
  outline: none;
  border: none;
  font-weight: lighter;
  background-color: #fff;
  margin-right: 2px;
  margin-left: 2px;
}
.button-wrapper button:hover:active {
  background-color: black;
}
.project2 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 30px;
}

img {
  border: 3px solid #fff;
  border-radius: 10px;
}

img:hover {
  width: 150px;
}

@media only screen and(max-width: 700px) {
  .projects-main h2 {
    color: white;
    text-align: center;
  }

  .project1 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 30px;
  }
  .calc-img {
    width: 100px;
    margin-bottom: 10px;
  }
  .button-wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex: 1;
  }
  .button-wrapper button {
    border-radius: 3px 3px 3px 3px;
    outline: none;
    border: none;
    font-weight: lighter;
    background-color: #fff;
    margin-right: 2px;
    margin-left: 2px;
  }
  .project2 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 30px;
  }

  img {
    border: 3px solid #fff;
    border-radius: 10px;
  }
}
