.background-img{

    background-image: url("./backGroundImg.jpg");
    position: fixed;
    min-width: 100%;
    min-height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.mainHeader{

    font-family: 'Bebas Neue', cursive;
    font-size:60px;
    color:white;
letter-spacing: 5px;



}   
.secondHeader{  
 
    font-family: 'Lobster',cursive;
    font-size: 40px;
    color:white;


}   

.headers{  
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
    
}

@media screen and (max-width: 750px){
    .headers{
        margin-top: 5%;
        position: absolute;
    }

    .mainHeader{
        font-size: 40px;
    }
    .secondHeader{
        font-size: 35px;
    }

}

@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lobster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@469&display=swap');