.contact-content{ 
    background-color: transparent;
    position: relative;
    border:none;
    
    
}
.contact-content h2{
    color:white;
    display: flex;
    justify-content: center;
    text-align: center;
    padding-bottom: 20px;
}

.contact-content p{
    margin-top: 15px;
    color:white;
    width: fit-content;
}
ul{
    color:white;
    text-decoration: none;
    list-style: none;
    padding:0;
    
}
ul li{
    text-decoration: none;
    text-decoration-style: none;
    margin-top: 5px;
    margin-bottom: 5px;
    text-align: left;
}